
table {
    table-layout: fixed;
    border-spacing: 5px;
  }
  
  th,
  td {
    padding: 10px;
    border-bottom: 1px solid #ddd;
    word-wrap: wrap;
    max-width: 140px;
    text-align: right;
  }
  
  th:nth-child(1),
  td:nth-child(1) {
    max-width: 180px;
    text-align: left;
  }
  
  tbody > tr:nth-child(odd) {background-color: #f2f2f2;}